*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #222;
}
.on{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  min-width: 200vh;
  background: radial-gradient(#555,#111);
}
.bulb{
  position: relative;
  width: 80px;
  height: 80px;
  background: #444;
  border-radius: 50%;
  z-index: 2;
}
.bulb::before{
  content: '';
  position: absolute;
  top:-50px;
  left: 22.5px;
  width: 35px;
  height: 80px;
  background: #444;
  border-top: 30px solid #000;
  border-radius: 10px;
}
.on .bulb::after{
  content: '';
  position: absolute;
  top: 50%;
  left: 50px;
  transform: translate(-50%,-50%);
  width: 120px;
  height: 120px;
  background: #fff;
  border-radius: 50%;
  filter: blur(40px);
   
}
.on .bulb{
  background: #fff;
  box-shadow: 0 0 50px #fff,
  0 0 100px #fff,
  0 0 150px #fff,
  0 0 200px #fff,
  0 0 250px #fff,
  0 0 300px #fff,
  0 0 350px #fff,

}
.on .bulb::before{
  background: #fff;
}
.on .bulb span:nth-child(1){
  box-shadow: 20px 20px 0 10px #fff;
}
.on .bulb span:nth-child(2){
  box-shadow: -20px 20px 0 10px #fff;
}
.bulb span:nth-child(1){
  position: absolute;
  display: block;
  top:-16px;
  left: -3.5px;
  width: 30px;
  height: 30px;
  background: transparent;
  transform: rotate(342deg);
  border-bottom-right-radius: 40px;
  box-shadow: 20px 20px 0 10px #444
}
.bulb span:nth-child(2){
  position: absolute;
  display: block;
  top:-16px;
  right: -4px;
  width: 30px;
  height: 30px;
  background: transparent;
  transform: rotate(18deg);
  border-bottom-left-radius: 40px;
  box-shadow: -20px 20px 0 10px #444
}
.wire{
  position: absolute;
  left: calc(50% - 2px);
  width: 4px;
  height: 60vh;
  background: #000;
  bottom: 50%;
  z-index: 1;
}
.switch{
  position: absolute;
  width: 100px;
  height: 100px;
  right: 50px;
  bottom: 50px;
  background: linear-gradient(#eee,#ccc,#eee);
  border: 3px solid #000;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.switch .btn{
  position: relative;
  width: 25px;
  height: 40px;
  background: linear-gradient(#777, #fff,#777);
  border: 2px solid #000;
  border-radius: 6px;
  cursor: pointer;
}
.switch .btn::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 85%;
  background: linear-gradient(#fff,#fff);
  border-radius: 4px;
}
.on .switch .btn::before{
  top: 15%;
}